.dark {
  // background-color: rgb(80, 75, 68);
  background-image:radial-gradient(ellipse at bottom, #7b7f8a 20%, #000000 100%);

  --primary-text-color: #d8d0d0;
  --title-text-color: #ff6b08;
  --secondary-title-color: #2c0b0b;
  --footer-text-color: #8b887f;
  --background-footer: #252525;
  --background-body: #797776;
    --modal-text-color: #f5e9c2;
  color: #f7f7f7;
  transition: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
body {
  transition: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.theme-btn {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition: 0.4s;

  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 10px;
  border-radius: 15px;
  transition: 0.3s;
  &:hover {
    transform: rotate(-20deg) scale(1.25);
    -webkit-transform: rotate(-20deg) scale(1.15);
  }

  @media screen and (max-width: 320px) {
    font-size: 20px;
    top: 175px;
    // right: 5%;
  }
  @media screen and (min-width: 320px) {
    font-size: 20px;
    top: 175px;
    // right: 5%;
  }
  @media screen and (min-width: 768px) {
    font-size: 30px;
    top: 160px;
    // right: 5%;
  }
  @media screen and (min-width: 1024px) {
    top: 160px;
    // right: 5%;
  }
  @media screen and (min-width: 1440px) {
    top: 160px;
    // right: 18%;
  }
  @media screen and (min-width: 2560px) {
    top: 160px;
    // right: 32%;
  }
}

