.translate{
  padding: 0 10px 0 10px;
  text-decoration: none;
  font-size: 16px;
  background-color: transparent;
  color: #f3efec;
  transition: 300ms;
  transition-timing-function: cubic-bezier(0.9, 0, 0.2, 1);
  &__span{
    font-size: 20px;
    color: #f3eae5;
  }
  &:hover{
    transform: scale(1.2);
    color: #ff6b08;
     transition: 300ms;
  transition-timing-function: cubic-bezier(0.9, 0, 0.2, 1);
  }
}
