.film-modal {
    width: 280px;
    padding: 48px 20px 40px 20px;
    background-color: var(--background-body);

    @media screen and (min-width: 768px) {
        display: flex;
        width: 704px;
        height: 552px;
        padding: 40px 72px 40px 36px;
    }

    @media screen and (min-width: 1024px) {
        width: 814px;
        height: 558px;
        padding: 40px 16px;
    }
}

.film-poster-wrapper {
    margin-bottom: 20px;
    
    @media screen and (min-width: 768px) {
        margin-right: 68px;
        margin-bottom: 0px;
    }

    @media screen and (min-width: 1024px) {
        margin-right: 16px;
    }
}

.film-poster-img {
    width: 240px;
    height: 357px;
    border-radius: 5px;

    @media screen and (min-width: 768px) {
        width: 250px;
    }

    @media screen and (min-width: 1024px) {
        width: 333px;
        height: 478px;
    }
}

.film-info-wrapper {
    width: 100%;
}

.film-modal-header {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.16;
    text-transform: uppercase;
}

.film-modal-item {
    display: flex;

    &:not(:last-child) {
        margin-bottom: 8px;
    }
}

.film-item-title {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    color: var(--modal-text-color);
    min-width: 50%;

    @media screen and (min-width: 768px) {
        min-width: 40%;
    }

    @media screen and (min-width: 1024px) {
        min-width: 30%;
    }
}

.film-item-desc {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
}

.vote-mod {
    padding: 1px 10px;
    border-radius: 5px;
    background-color: var(--background-footer);

    &.color {
        background-color: var(--secondary-bg-color);
        color: var(--secondary-title-color);
    }
}

.original-title {
    text-transform: uppercase;
    line-height: 1.33;
}

.genre-desc {
    line-height: 1.33;
}

.film-modal-about {
    margin-top: 20px;
    margin-bottom: 8px;
    font-weight: 500;
}

.film-modal-overview {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;

    @media screen and (max-width: 1024px) {
        height: 25%;
        overflow-y: auto;
    }
}

.film-modal-btns {
    margin-top: 20px;
}

.film-modal-btns {
    display: flex;
    
    @media screen and (min-width: 1024px) {
        margin-left: 8px;
    }
}

.modal-btn-watched {
    @extend .button;
    margin-right: 20px;
}

.modal-btn-queue {
    @extend .button;
}

.film-modal-close {
    position: absolute;
    top: 8px;
    right: 8px;
    background-color: transparent;
    cursor: pointer;
    

    &:hover .modal-close-ico,
    &:focus .modal-close-ico {
        @extend %animation;
         stroke: var(--secondary-bg-color);
    }
}

